.content {
    box-sizing: border-box;
    width: 100%;
    transform: translate(50%);
    position: relative;
}
.gameBoard {
    transform: translateX(-50%);
    margin: 25px;
    width: 500px;
    height: 500px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 20px;
    box-sizing: border-box;
}
.gameCell {
    border: 1px solid #ccc;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    font-size: 80px;
    border-radius: 5px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.X {
    color: rgba(39, 1, 255, 0.986);
}
.O {
    color: rgba(252, 0, 0, 0.986);
}
.reset {
    border-radius: 5px;
    display: inline-block;
    padding: 10px 25px;
    font-weight: bold;
    border: none;
    background-color: rgba(6, 201, 244, 0.559);
    transform: translateX(-50%);
    cursor: pointer;
}
.noti {
    font-size: 50px;
    z-index: 10;
    color: rgba(249, 3, 97, 0.993);
    font-weight: bold;
    background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
    width: 520px;
    transform: translateX(-45%);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 35%;
}
